.blog {
  padding-top: 80px;
  padding-bottom: 100px;
}

.blog__info {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  display: flex;
}

.blog__info__block {
  cursor: pointer;
  border: .5px solid #bfbfbf;
  border-radius: 4px;
  flex: 0 30%;
  margin-right: 10px;
  transition: all .3s;
}

.blog__info__block:hover {
  margin-top: -15px;
  box-shadow: 0 5px 20px #0003;
}

.blog__info__block:last-child {
  margin-right: 0;
}

.blog__info__image {
  height: 248px;
  overflow: hidden;
}

.blog__info__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 4px 4px 0 0;
}

.blog__info__item {
  padding: 24px 29px 32px;
}

.blog__info__item__title {
  color: #222;
  margin-bottom: 16px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 26px;
}

.blog__info__item__text {
  color: #444;
  margin-bottom: 16px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
}

.blog__info__item__more a {
  color: #086cf9;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
  transition: color .1s linear;
}

.blog__info__item__more a:hover {
  color: #0047ab;
}

.blog__button {
  text-align: center;
}

.blog__button__item {
  padding: 16px 40px;
  font-weight: 500;
}

@media (max-width: 1000px) {
  .blog__info__block {
    flex: 31%;
    margin-right: 20px;
  }

  .blog__info__item {
    padding: 20px;
  }

  .blog__info__item__title {
    margin-bottom: 14px;
    font-size: 18px;
    line-height: 22px;
  }

  .blog__info__item__text {
    margin-bottom: 14px;
    font-size: 14px;
    line-height: 19px;
  }

  .blog {
    padding-top: 60px;
    padding-bottom: 80px;
  }

  .blog__button__item {
    padding: 10px 34px;
  }
}

@media (max-width: 818px) {
  .blog__info {
    flex-wrap: wrap;
    justify-content: center;
  }

  .blog__info__block {
    flex: 0 46%;
    margin-bottom: 20px;
    margin-right: 20px;
  }

  .blog__info__item {
    padding: 20px 26px 22px;
  }

  .blog {
    padding-bottom: 60px;
  }
}

@media (max-width: 570px) {
  .none2 {
    display: none;
  }

  .blog__info__block {
    flex: 0 80%;
    margin-bottom: 0;
    margin-right: 0;
  }
}

@media (max-width: 400px) {
  .blog {
    padding-bottom: 40px;
  }

  .blog__info__block {
    flex: 0 90%;
  }

  .blog__info__item {
    padding: 18px 22px 19px;
  }
}

